// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            @include mkdfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include mkdfTransition(opacity .2s ease-in-out);
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
    @include mkdfRelativeHolderLayout(top);
    margin: 0;
    clear: both;

    ul {
        @include mkdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 23px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                font-weight: 600;

                &:after {
                    width: 46px;
                    height: 46px;
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    border: 2px solid $default-border-color;
                    border-radius: 100%;
                    @include mkdfTransform(translate(-50%, -50%));
                    opacity: 0;
                    @include mkdfTransition(opacity .2s ease);
                }

                &:hover {
                    color: inherit;

                    &:after {
                        opacity: 1;
                        @include mkdfTransition(opacity .2s ease);
                    }
                }
            }

            &.mkdf-pag-active {
                a {
                    &:after {
                        opacity: 1;
                        @include mkdfTransition(opacity .2s ease);
                    }
                }
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next,
            &.mkdf-pag-first,
            &.mkdf-pag-last {
                margin: 0 2px;
                top: -2px;

                a {
                    font-size: 24px;

                    &:after {
                        display: none;
                    }

                    &:hover {
                        color: $first-main-color;
                    }

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            &.mkdf-pag-first,
            &.mkdf-pag-last {
                top: 1px;
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-first{
                a {
                    &:hover {
                        svg {
                            @include mkdfAnimation(navglitch .6s);
                        }
                    }
                }
            }
            &.mkdf-pag-next,
            &.mkdf-pag-last{
                a {
                    &:hover {
                        svg {
                            @include mkdfAnimation(navglitchreverse .6s);
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.mkdf-pag-prev {
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    font-weight: 600;
                    color: $default-heading-color;

                    &:after {
                        width: 46px;
                        height: 46px;
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        border: 2px solid $default-border-color;
                        border-radius: 100%;
                        @include mkdfTransform(translate(-50%, -50%));
                        opacity: 1;
                        @include mkdfTransition(opacity .2s ease);
                    }

                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:after {
                            display: none;
                        }

                        &:hover {
                            color: $first-main-color;
                        }

                        &:before {
                            content: '';
                            position: relative;
                            top: 7px;
                            display: block;
                            width: 26px;
                            height: 9px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            background-image: url('../img/arrow-left-small.svg');
                        }

                        &:hover {
                            &:before {
                                @include mkdfAnimation(navglitch .6s);
                            }
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            background-image: url('../img/arrow-right-small.svg');
                        }
                        &:hover {
                            &:before {
                                @include mkdfAnimation(navglitchreverse .6s);
                            }
                        }
                    }
                }
            }
        }
    }
}

@keyframes navglitch {
    0% {
        @include mkdfTransform(translateX(0));
    }
    50% {
        @include mkdfTransform(translateX(-5px));
    }
    100% {
        @include mkdfTransform(translateX(0));
    }
}

@keyframes navglitchreverse {
    0% {
        @include mkdfTransform(translateX(0));
    }
    50% {
        @include mkdfTransform(translateX(5px));
    }
    100% {
        @include mkdfTransform(translateX(0));
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 14px;
    line-height: 1em;
    letter-spacing: 0.03em;
    font-weight: 600;
    outline: none;
    box-sizing: border-box;
    @include mkdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 15px 25px;

    } @else if ($size == 'medium') {
        padding: 20px 38px;

    } @else if ($size == 'large') {
        padding: 25px 50px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 20px 26px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 2px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 2px solid #d7e3f7 $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

