// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding 0;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-heading-color;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #a6a8ad;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include mkdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

