// heading mixins - start

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include mkdfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfHeadingStyle($with-heading: null, $with-color: true) {
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}

@mixin mkdfBody() {
    font-family: $default-text-font;
    font-size: 15px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

@mixin mkdfH1() {
    font-size: 80px;
    line-height: 0.9375em;
    font-weight: 700;
    letter-spacing: -.04em;
}

@mixin mkdfH2() {
    font-size: 60px;
    line-height: 1.08em;
    font-weight: 700;
    letter-spacing: -.04em;
}

@mixin mkdfH3() {
    font-size: 50px;
    line-height: 1.02em;
    font-weight: 700;
    letter-spacing: -.04em;
}

@mixin mkdfH4() {
    font-size: 36px;
    line-height: 1.28em;
    font-weight: 700;
    letter-spacing: -.04em;
}

@mixin mkdfH5() {
    font-size: 27px;
    line-height: 1.07em;
    font-weight: 700;
    letter-spacing: -0.04em;
}

@mixin mkdfH6() {
    font-size: 20px;
    line-height: 1.2em;
    font-weight: 700;
    letter-spacing: 0;
}

// heading mixins - end

